<template>
  <div id="app">
    <input type="button" value="测试push方法" @click="pushHandle">
    <input type="button" value="测试go方法" @click="gohandle">
    <input type="button" value="测试replace方法" @click="replaceHandle"> 
    <transition name='ketang' mode="out-in">
      <router-view></router-view>
    </transition>
    
  </div>
</template>

<script>

export default {
  methods: {
    pushHandle () {
      // 这个方法会向 history 栈添加一个新的记录，所以，当用户点击浏览器后退按钮时，则回到之前的 URL。
      this.$router.push({
        path: '/project'
      })
    },
    gohandle () {
      // this.$router.go(1)
      // this.$router.go(-1)
      this.$router.go(-5)
    },
    replaceHandle () {
      // 跟 router.push 很像，唯一的不同就是，它不会向 history 添加新记录，而是跟它的方法名一样 —— 替换掉当前的 history 记录。
      this.$router.replace({
        path: '/project'
      })
    }
  }
}
</script>

<style>
.miaov-enter {
  opacity: 0;
  transform: translateX(5500px)
}
.miaov-enter-active {
  transition: 1s;
}
.miaov-enter-to {
  opacity: 1;
  transform: translateX(0)
}

.miaov-leave {
  opacity: 1;
  transform: translateX(0)
}
.miaov-leave-active {
  transition: 1s;
}
.miaov-leave-to {
  opacity: 0;
  transform: translateX(-5500px)
}

.ketang-enter {
  opacity: 0;
}
.ketang-enter-active {
  transition: 1s;
}
.ketang-enter-to {
  opacity: 1;
}

.ketang-leave {
  opacity: 1;
}
.ketang-leave-active {
  transition: 1s;
}
.ketang-leave-to {
  opacity: 0;
}
</style>



