<template>
  <div>
    <p>{{ $route.params.id }}</p>
    <ul>
      <li>
        <input type="button" value="用户1" @click="locationHandle(1)" />
        <input type="button" value="用户2" @click="locationHandle(2)"/>
        <input type="button" value="用户3" @click="locationHandle(3)"/>
      </li>
    </ul>
    <p>姓名：{{info.userName}}</p>
    <p>性别：{{info.sex}}</p>
    <p>年龄：{{info.age}}</p>
  </div>
</template>
<script>
  // 使用同一个组件 /users/1  /users/2
  // 需要拿到动态的路径的信息
  // 在组件中拿到这个路由信息对象 $route

  let data = {
    '1': {
      userName: 'leo1',
      sex: '男',
      age: 30
    },
    '2': {
      userName: 'leo2',
      sex: '男',
      age: 30
    },
    '3': {
      userName: 'leo3',
      sex: '男',
      age: 30
    }
  }

  export default {
    data () {
      return {
        info: {
          userName: 'XXXXX',
          sex: 'XXXXX',
          age: 'XXXXX'
        }
      }
    },
    beforeRouteUpdate (to, from, next) {
      console.log('我更新了')
      next()
    },
    /* beforeRouteLeave (to, from, next) {
      console.log('离开了')
      let bl = confirm('真的要离开吗')
      if (bl) {
        next()
      }
    }, */
    watch: {
      $route () {
        console.log(this.$route)
        let id = this.$route.params.abc
        if (data[id]) {
          this.info = data[id]
        }
      }
    },
    mounted () {
      console.log(this.$route)  // 当前访问的路径的路由信息
      console.log(this.$route.params.abc)  // 保存的时候路径 key:value
      let id = this.$route.params.abc
      if (data[id]) {
        this.info = data[id]
      }
    },
    methods: {
      locationHandle (id) {
        // 怎么改变地址栏的地址呢？
        // 跳转链接 调用push 使用$router
        // this.$router.push('/project')
        this.$router.push({
          path: '/users/' + id
        })
      }
    }
  }
</script>
<style>
  p {
    font-size: 50px;
  }
  input {
    width: 130px;
    height: 50px;
    font-size: 30px;
  }
</style>




