import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Home from '@/components/home'
import Layout from '@/components/layout'
import Users from '@/components/users'

import Project from '@/components/project'
import Doc from '@/components/doc'

import noFound from '@/components/404'

// 想要再每一个组件中拿到路由信息，要通过
// $route 当前路由信息 对象
// $router 路由实例

let router = new VueRouter({
  mode: 'history',  // http://miaov.com/project
  base: '/miaov-class/',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/backendManager',
      component: Layout,
      children: [
        {
          path: '',    // 默认的子路由  /backendManager/
          // component: Project
          redirect: {name: 'Project'}
        },
        {
          // 要注意，以 / 开头的嵌套路径会被当作根路径。 这让你充分的使用嵌套组件而无须设置嵌套的路径。
          path: '/project',   // 会被当做根路径 /project
          name: 'Project',
          component: Project/* ,
          beforeEnter (to, from, next) {  // 只有进入到/project才执行
            console.log(123)
            if (from.path === '/doc') {
              next()
            } else {
              next(false)
            }
          } */
        },
        {
          path: '/doc',  // 会被当做根路径 /doc
          name: 'Doc',
          component: Doc
        },
        {
          path: '/users/:abc?',  // :动态路径 ? 可以出现一次或0次 /users/1 /users/2
          component: Users
        }
      ]
    },
    {
      path: '*',
      component: noFound
    }
  ]
})

// 全局的导航钩子 只要访问任意个一个路径，都会触发这个钩子函数
/* router.beforeEach((to, from, next) => {
  console.log(to) // 即将要进入的目标 路由对象
  console.log(from) //  当前导航正要离开的路由
  // 123
  console.log('hello')
  // next() 进入到组件中，必须调用这个函数
  next()
})

router.afterEach(route => {
  // ...
  console.log(route)
}) */

export default router



